export default {
  layout: "Emp-content",
  data() {
    return {
      name: "",
      admissionFee: "",
      monthlyFee: "",
      classes: null,
      error: "",
    };
  },
  created() {
    this.getClass();
  },
  computed: {
    totalFee() {
      if (this.admissionFee && this.monthlyFee) {
        return parseInt(this.admissionFee) + parseInt(this.monthlyFee);
      }
    },
  },
  methods: {
    createClass() {
      this.$axios
        .post("/accounts/class", {
          name: this.name,
          admissionFee: this.admissionFee,
          monthlyFee: this.monthlyFee,
        })
        .then((response) => {
          this.classes = [
            ...this.classes,
            {
              id: this.classes.length + 1,
              name: this.name,
              admission_fee: this.admissionFee,
              monthly_fee: this.monthlyFee,
            },
          ];
          (this.name = ""),
            (this.admissionFee = ""),
            (this.monthlyFee = ""),
            this.$toaster.success(response.data.message);
          this.$router.push("/account/class");
        })
        .catch((error) => {
          if (error.response.status === 422) {
            this.error = error.response.data.errors;
          }
          console.log(error);
        });
    },
    getClass() {
      this.$axios
        .get("/accounts/class")
        .then((response) => {
          this.classes = response.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
